body{
    text-align: center;
    min-width: 380px;
    background-color: #003300;
}
.header{
    background-color: #eaffea;
    display: flex;
    height: auto;
    flex-direction: column;
    justify-content: center;
}
.header > img{
    align-self: center;
    padding: 45px 0px 0px 0px;
}
h1{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: 300;
    font-size: 2.5em;
    letter-spacing: -5px;
    line-height: 40px;
}
.header > h1{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: 300;
    font-size: 4rem;
    letter-spacing: -7.5px;
    line-height: 60px;
}
p{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: normal;
    font-size: 1rem;
}
.navbar{
    position: fixed;
    background-color: #003300;
    align-content: center;
    top: 0px;
    right: 0px;
}
.navbar > ul{
    display: flex;
    margin: 10px;
}
.navbar > li{
    /*display: block;*/
    list-style: none;
}
.link{
    background-color: #003300;
    color: #eaffea;
    padding: 10px;
}
.link:visited{
    background-color: #003300;
    color: #eaffea;
}
.link:hover{
    background-color: #eaeaea;
    color: #003300;
}
.rodape{
    position: fixed;
    background-color: #003300;
    color: #eaffea;
    bottom: 0px;
    right: 0px;
}
.rodape > p{
    font-family: 'Roboto Mono', sans-serif;
    font-size: 0.75rem;
    font-weight: 300;
}
#projetos > p {
    list-style: none;
    text-align: left;
    padding: 10px;
}
#projetos{
    width: 100%;
    height: auto;
    background-color: #daffda;
    padding-bottom: 16px;
}
#sobre{
    width: 100%;
    height: auto;
    background-color: #caffca;
}
#sobre > p{
    text-align: left;
    padding: 0px 10px 10px 10px;
}

.link2{
    padding: 10px 10px 10px 10px;
    font-size: 1.5rem;
}
.link2:visited{
    font-size: 1.5rem;
}
.link2:hover{
    background-color: #003300;
    color: #eaeaea;
}
#blog{
    background-color: #eaffea;
    margin-top: 10px;
}
#blog > ul{
    list-style: none;
}
#blog > h1{
    line-height: 60px;
}
#poste{
    background-color: #eaffea;
    color: #003300;
    text-align: left;
    padding: 10px;
}
h2{
    font-family: 'Roboto Mono', sans-serif;
    line-height: 5px;
}
.grid-container{
    display: grid;
}
#sitetags{
    background-color: #eaffea;
    text-align: left;
}
@media(max-width: 500px){
    #projetos{
        padding-bottom: 0px;
    }
    .navbar {
        position: fixed;
        width: 100%;
    }
    .rodape {
        position: inherit;
        white-space: pre-line;
    }
}
@media(min-width: 700px){
    .grid-container{
        grid-template-columns: 20% 80%;
    }
    #blog{
        margin-top: 0px;
        margin-left: 5px;
    }
}
